.popup-error {
  position: absolute;
  top: 0;
  left: calc(50% - 155px);
  background-color: $color-error;
  border-radius: 5px;
  width: 310px;
  height: 100px;
  z-index: 100;
  transform: translateY(-250px);
  transition: transform 0.5s ease;


  &--show {
    transform: translateY(100px);
  }
}

.popup-erorr__content {
  display: flex;
  align-items: flex-start;
  padding: 5px;
}

.popup-error__descr {
  margin: 0;
  text-align: left;
  padding-left: 30px;
  color: $color-default-white;
  font-weight: 700;
}

.popup-error__btn {
  background-color: red;
  border: none;
}
