.submenu {
  padding: 15px;
  background-color: $color-default-white;
  position: absolute;
  width: 100%;
  left: 0;
  top: 52px;
  z-index: 100;
  display: none;

  &--show {
    display: block;
  }
}

.submenu__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.submenu__item {
  padding-top: 5px;
  padding-bottom: 5px;
  cursor: pointer;
  padding-left: 10px;
  transition: background-color 0.3s ease;

  &:hover {
    background-color: $color-carla;
  }
}
