.data__header-list {
  display: grid;
  grid-template-columns: 29.8% 21.2% 9.61% 1fr 32px;
  column-gap: 10px;
  list-style: none;
  margin: 0;
  padding: 0 0 20px;
  border-bottom: 1px solid $color-default-black;
}

.data__header-item {
  padding: 10px 5px 10px 5px;
  background-color: $color-carla;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.data__fields-list {
  display: grid;
  grid-template-columns: 29.8% 21.2% 9.61% 1fr 32px;
  column-gap: 10px;
  list-style: none;
  padding: 20px 0;
  margin: 0;
  border-bottom: 1px solid $color-default-black;
}

.data__fields-list input,
.data__fields-list select {
  width: 100%;
  height: 100%;
  border: none;
  padding: 10px 5px 10px 5px;
  background-color: $color-default-white  ;


  &::placeholder {
    font-size: 14px;
    line-height: 22px;
    color: $color-very-light-gray;
  }
}

.data__fields-list textarea {
  width: 100%;
  border: none;
  padding: 10px 5px 10px 5px;
  height: 42px;
  font-size: 12px;

  &::placeholder {
    font-size: 14px;
    line-height: 22px;
    color: $color-very-light-gray;
  }
}

.data__field-item {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  position: relative;
  height: 42px;

  &:last-child {
    box-shadow: none;
  }
}

[data-button="close-list"] {
  border: none;
  cursor: pointer;
  color: $color-gainsboro;
  background-color: transparent;

  &:hover,
  &:focus {
    opacity: 0.5;
    color: $color-carla;
    border: 1px solid $color-carla;
    outline: none;
    border: none;
  }
}

.data__field--error {
  outline: 2px solid $color-error;
}
