.site-navigation__list {
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  list-style: none;
  margin: 0;
  padding: 0;

  @include vp-767 {
    display: none;
  }
}

.site-navigation__link {
  font-size: 14px;
  line-height: 18px;
  transition: color 0.3s ease;
}

.site-navigation__item:hover .site-navigation__link {
  color: $color-dim-gray;
}
