.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh)); }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */ }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.scroll-lock-ios {
  position: fixed;
  overflow: hidden; }

.scroll-lock {
  overflow: hidden; }

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none; }
  .no-scrollbar::-webkit-scrollbar {
    display: none; }

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important; }

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 40px; }

.navigation {
  display: grid;
  grid-template-columns: 1fr auto 1fr; }
  @media (max-width: 767px) {
    .navigation {
      display: flex;
      justify-content: space-between; } }

@media (max-width: 767px) {
  .navigation__logo {
    width: 100px;
    grid-column-start: 1;
    grid-column-end: 2;
    padding-top: 8px; } }

.site-navigation__list {
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  list-style: none;
  margin: 0;
  padding: 0; }
  @media (max-width: 767px) {
    .site-navigation__list {
      display: none; } }

.site-navigation__link {
  font-size: 14px;
  line-height: 18px;
  transition: color 0.3s ease; }

.site-navigation__item:hover .site-navigation__link {
  color: #6e6e6e; }

.user-navigation {
  flex-grow: 1; }

.user-navigation__list {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: flex-end;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0; }
  @media (max-width: 767px) {
    .user-navigation__list {
      gap: 25px; } }

.user-navigation__item {
  position: relative; }

@media (max-width: 767px) {
  [data-item="search"],
  [data-item="account"],
  [data-item="favorite"] {
    display: none; } }

@media (max-width: 767px) {
  [data-item="bag"] svg {
    width: 28px;
    height: 28px; } }

[data-item="favorite"]::after,
[data-item="bag"]::after {
  font-size: 10px;
  line-height: 8px;
  color: white;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 15px;
  height: 15px;
  background-color: #000000;
  border-radius: 50%;
  top: -3px;
  right: -7px; }

[data-item="favorite"]::after {
  content: attr(data-value); }

[data-item="bag"]::after {
  content: attr(data-value); }

.user-navigation__link {
  transition: color 0.3s ease; }

.user-navigation__item:hover .user-navigation__link {
  color: #6e6e6e; }

[data-item="burger-menu"] {
  display: none; }
  @media (max-width: 767px) {
    [data-item="burger-menu"] {
      display: block; } }

[data-item="burger-menu"] button {
  border: none;
  background-color: transparent;
  padding: 0;
  cursor: pointer; }

.header {
  padding-top: 30px;
  padding-bottom: 30px;
  background-color: #f6facd; }

.main-page {
  padding-top: 40px;
  padding-bottom: 40px; }

.main-page__wrapper {
  display: grid;
  grid-template-columns: 20% 1fr;
  column-gap: 20px;
  min-height: 74vh; }

.main-menu {
