  margin: 0;
  padding-left: 70px; }

.main-menu__item {
  position: relative; }
  .main-menu__item::before {
    content: "";
    position: absolute;
    width: 10px;
    height: 10px;
    top: 8px;
    left: -20px;
    border-radius: 50%;
