.main-menu {
  border-right: 1px solid $color-default-black;
}

.main-menu__title {
  font-size: 24px;
  line-height: 22px;
  padding-bottom: 15px;
  padding-left: 40px;
  border-bottom: 1px solid $color-default-black;
}

.main-menu__list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  list-style: none;
  margin: 0;
  padding-left: 70px;
}

.main-menu__item {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    width: 10px;
    height: 10px;
    top: 8px;
    left: -20px;
    border-radius: 50%;
    background-color: $color-carla;
  }
}
