.user-navigation {
  flex-grow: 1;

}

.user-navigation__list {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: flex-end;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;

  @include vp-767 {
    gap: 25px;
  }
}

.user-navigation__item {
  position: relative;
}

[data-item="search"],
[data-item="account"],
[data-item="favorite"] {

  @include vp-767 {
    display: none;
  }
}

[data-item="bag"] svg {

  @include vp-767 {
     width: 28px;
  height: 28px;
  }
}

[data-item="favorite"]::after,
[data-item="bag"]::after {
  font-size: 10px;
  line-height: 8px;
  color: white;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 15px;
  height: 15px;
  background-color: $color-default-black;
  border-radius: 50%;
  top: -3px;
  right: -7px;
}

[data-item="favorite"]::after {
  content: attr(data-value);
}

[data-item="bag"]::after {
  content: attr(data-value);
}


.user-navigation__link {
  transition: color 0.3s ease;
}

.user-navigation__item:hover .user-navigation__link {
  color: $color-dim-gray;
}

[data-item="burger-menu"] {
  display: none;

  @include vp-767 {
    display: block;
  }
}

[data-item="burger-menu"] button {
  border: none;
  background-color: transparent;
  padding: 0;
  cursor: pointer;

}
