.content__search {
  display: flex;
  justify-content: center;
  margin-bottom: 40px;
}

.content__search-wrapper {
  position: relative;
}

.content__search input {
  width: 592px;
  padding: 13px 8px 13px 50px;
  border-radius: 24px;
  background-color: $color-carla;
  border: none;


  &::placeholder {
    font-size: 14px;
    line-height: 22px;
    color: $color-very-light-gray;
  }
}

.content__search button {
  position: absolute;
  top: 8px;
  left: 0px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.content__buttons {
  display: flex;
  justify-content: space-between;
  padding-top: 50px;
}

.content__buttons button {
  font-size: 24px;
  line-height: 24px;
  color: $color-default-white;
  background-color: $color-default-black;
  padding: 10px 50px;
  border: none;
  border-radius: 30px;
  cursor: pointer;

  &:hover,
  &:focus {
    opacity: 0.7;
    color: $color-carla;
  }
}
